export default {
	data() {
		return {};
	},
	methods: {
		//获取用户授权信息
		getUserInfo(res) {
			console.log(res);
			// uni.setStorage({
			// 	key: 'userId',
			// 	data: res.detail.userInfo.avatarUrl,
			// 	success: () => {
			// 		console.log('userId success');
			// 	}
			// });
			uni.setStorage({
				key: 'userName',
				data: res.detail.userInfo.nickName,
				success: () => {
					console.log('userName success');
				}
			});
			uni.setStorage({
				key: 'userAvatar',
				data: res.detail.userInfo.avatarUrl,
				success: () => {
					console.log('userAvatar success');
				}
			});
			uni.login({
				provider: 'weixin',
				success: loginRes => {
					console.log(loginRes.code);
					// 获取用户信息
					let _this = this;
					let data = {
						js_code: loginRes.code,
						userName: res.detail.userInfo.nickName,
						userPhoto: res.detail.userInfo.avatarUrl
					};
					_this.$ajax
						.postJSON({
							url: '/wechat/userLogin',
							data: data,
						})
						.then(res => {
							uni.showToast({
								title:res.data.message,
								icon:'none'
							})
							console.log(res.data.data.wxopenId);
							uni.setStorage({
								key: 'openId',
								data: res.data.data.wxopenId,
								success: function(res) {}
							});
							uni.setStorage({
								key: 'userId',
								data: res.data.data.id,
								success: function(res) {
									console.log(res);
									uni.switchTab({
										url: '/pages/tabBar/home'
									});
								}
							});
						}).catch((error)=>{
							console.log(error)
						})
				}
			});
		}
	}
};
